.MenuToggle{
    position: fixed;
    top: 40px;
    left: 40px;
    font-size: 20px;
    color: white;
    z-index: 100;
    cursor: pointer;
    -webkit-transition: opacity, left .22s ease-in;
    -o-transition: opacity, left .22s ease-in;
    transition: opacity, left .22s ease-in;
}

.MenuToggle:hover{
    opacity: .7;
}

.MenuToggle.open{
    left: 320px;
}


