.Button{
    display: inline-block;
    padding: 10px 20px;
    border-radius: 4px;
    border: 1px solid #ccc;
    color: black;
    margin-right: 15px;
    text-transform: uppercase;
    font-width: bold;
    font-size: 12px;
}

.Button:focus{
    outline: none;
}

.Button:active{
    -webkit-box-sizing: unset;
            box-sizing: unset;
    -webkit-box-shadow: 2px 2px 1px rgba(0,0,0,.3);
            box-shadow: 2px 2px 1px rgba(0,0,0,.3);
}

.Button:disabled{
    color: #000;
    background: #ccc;
    cursor: not-allowed;
}

.success{
    background: rgba(161, 240, 69, 1);
    color: black;
}

.error{
    background: rgba(240, 87, 108, 1);
}

.primary{
    background: #2884f6;
    color: #fff;
}


